<template>
	<div class="wrapper ">
		<div class="container">
			<div class="row justify-content-center">
				<div class="col-12 col-md-5 col-xl-4 my-5">
					<div v-if="errors.length">
						<div v-for="error in errors" :key="error.id" class="alert alert-danger fade show text-center" role="alert">
							{{ error['msg'] }}
                 		</div>
					</div>
					<div class="card">
						<div class="card-body">
							<h1 class="display-4 text-center mb-3">Rage</h1>
							<p class="text-muted text-center mb-5">
								Enter a password to sign up.
							</p>
							<form id="register-form" v-on:submit.prevent="processRegister" method="post">
								<div class="form-group">
									<label>Username</label>
									<input type="text" class="form-control" value="Cloud" disabled>
								</div>
								<div class="form-group">
									<div class="input-group input-group-merge">
										<input type="password" name="password" v-model="password" class="form-control form-control-appended" placeholder="Enter your password">
										<div class="input-group-append">
											<span class="input-group-text">
												<i class="fe fe-eye"></i>
											</span>
										</div>
									</div>
								</div>
                                <div class="form-group">
									<div class="input-group input-group-merge">
										<input type="password" name="confirmPassword" v-model="passwordConfirm" class="form-control form-control-appended" placeholder="Confirm your password">
										<div class="input-group-append">
											<span class="input-group-text">
												<i class="fe fe-eye"></i>
											</span>
										</div>
									</div>
								</div>
								<button class="btn btn-lg btn-block btn-primary mb-3">Sign up</button>
							</form>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
    export default {
        data: function() {
            return {
                password: '',
                passwordConfirm: '',
                errors: []
            }
        },
        methods: {
            processRegister: function(e) {
                this.errors = [];

                // if all inputs have been filled
                if(this.password && this.passwordConfirm) {
                    
                    // check if both passwords match
                    if(this.password != this.passwordConfirm) {
                        // passwords do not match
                        return this.errors.push(new Object({ 'passwordMatch': true, 'msg': 'Those passwords to not match.'}));
                    }

                    // data is validated and being passed to client
                    mp.trigger('sendData', JSON.stringify({ password: this.password, action: 'register' }));
                }

                // if password has not been filled
                if(!this.password) {
                    this.errors.push(new Object({ 'msg': 'A password is required.'}));
                }
                
                // if password confirm has not been filled
                if(!this.passwordConfirm) {
                    this.errors.push(new Object({ 'msg': 'You need to verify your password.'}));
                }

                this.$forceUpdate();

                e.preventDefault();
            }
        }
    }
</script>

<style lang="scss" scoped>
    
</style>